import React from 'react';
import "./HomePage.css";
import image from '../../assets/background-image.jpg'
export default function Homepage() {
    return (
        <div className="p-home">
            <div className="p-home-bg-img">
                <img  className="p-home-img" src={image}></img>
            </div>
            <div className="p-home-title">
                <h1 className="display-1 ">Homeowner Association Management System</h1>
                <h2 className="display-7 ">All you need to handle your building </h2>
            </div>
        </div>
    )
}
